import gsap, { Expo } from "gsap/all";
import { useEffect, useRef } from "react";
const Nav = () => {
    const navRef = useRef(null);
    useEffect(() => {
        gsap.from('.nav-link',{duration: 0.2, opacity: 0, y: '-100%', stagger:0.1, delay: 0.2, ease: Expo.easeInOut});
        gsap.from('.logo',{duration: 0.2, opacity: 0, y: '-100%', stagger:0.1});

    })
    return (
        <nav className="navbar">
            <div className="logo">OA</div>

            <ul ref={navRef} className="nav-links">
                <li className="nav-link">Home</li>
                <li className="nav-link">About</li>
                <li className="nav-link">Portfolio</li>
                <li className="nav-link">Contact</li>
            </ul>
        </nav>
    )
}

export default Nav;