const Contact = () => {
    return(
        <div className="contact">
            <h3 className="head-con">I'd love to hear from you!</h3>
        <form method="GET">
            <div className="container">
            <input type="text" name="name" placeholder="Name" required/>
            <input type="email" name="email" placeholder="Email" required/>
            <textarea name="massege" placeholder="how can we help you" required></textarea>
            <button className="submit" type="submit">Contact me</button>
            </div>
        </form>
        </div>
    )
}
export default Contact;