import { gsap } from "gsap/all";
import { useRef, useEffect } from "react";

const About = () => {
    const aboutRef = useRef(null);
    useEffect(() => {
    gsap.from(aboutRef.current, {scrollTrigger:'.about-p', duration: 1, opacity: 0, x: 100});
        
    }, [aboutRef]);
    return (
        <div className="about">
            <div className="con">
            <h1 className="about-title">About</h1>
            <div ref={aboutRef} className="about-p">
            <p>Hi, my name is Omar Ayman, a Web Designer<br />
and Front-End Developer from Egypt who<br />
loves design and making unique stuff.</p>
        </div>
        </div>
        </div>
    )
}

export default About;