import { useRef, useEffect } from "react";
import { gsap } from "gsap/all";
import { Expo } from "gsap";
const Hero = (props) => {
    const textRef = useRef(null);
    useEffect(() => {
        gsap.from('.heroaf', {duration: 0.5, x: '-10%', opacity: 0, ease: Expo.easeInOut, delay: 0.2});
        gsap.from('.hero-text', {duration: 1, x: '-10%', opacity: 0, stagger: 0.2, ease: Expo.easeInOut, delay: 0.2});

    })
    return (
        
        <div className="hero-sec">
            
            <div ref={textRef} className="hero-titles-sec">
                <h1 className="hero-title hero-text">Hi,</h1>
                <h1 className="hero-title hero-text">I'm <span className="name">Omar</span></h1>
                <h1 className="hero-title hero-text">Web Developer.</h1>
                <p  className="tech-langs hero-text">PHP  /  JavaScript  /  React</p>
            </div>
        <span className="heroaf"></span>
        </div>

    )
}
export default Hero;