import opinions  from "./opinions";

const Customer = () => {

    const sliderShow = (i) => {
        let x = opinions[i];

        return(
            <div className="op">
                <h3>{x.name}</h3>
                <p>{x.massege}</p>
            </div>)
    }

    return (
        
        <div className="cus">
            <div className="cus-op">
                {sliderShow(1)}
            </div>
        </div>
        
    )
}
export default Customer;