import Project from "./Project"
import images from './images';
import { useRef, useEffect } from "react";
import { gsap } from "gsap/all";
const Projects = () => {
    const projectsRef = useRef(null);
    
    useEffect(() => {
        gsap.from('.project', {duration: 0.5, scrollTrigger: '.projects-sec', opacity: 0, stagger: 0.5})}, [projectsRef]);
    return (
        <div className="projects-section">
            <h1 className="about-title">my projects</h1>

        <div ref={projectsRef} className="projects-sec">
            

            {images.map(img => <Project  key={img.key} name={img.title} skills={img.skills} src={img.src} />)}
        </div>
        </div>
    )
}

export default Projects;
// {images.map(img => <img key={img.src} src={img.src} alt="portfolio" />)}